<template>
	<main>
		
		<transition enter-active-class="animated fadeInLeft">
			<div v-show="showLogin">
				<header>
					<mt-header title="登录"  @click="toLogin()">
						<i class="iconfont" slot="left" @click="back">&#xe60e;</i>
						<span slot="right" @click="toRegister()">注册</span>
					</mt-header>
				</header>
				
				<!--<header>
					<i class="iconfont" @click="back">&#xe60e;</i>
					<span class="login" @click="toLogin()">{{msg1}}</span>
					<span class="register" @click="toRegister()">{{msg2}}</span>
				</header>-->
				
				<div class="loginBody">
					<div class="loginInfo">
						<input type="text" placeholder="Email/POCO号/手机" />
						<input type="password" placeholder="密码 " />
					</div>
					<button>登录</button>
					
					<div class="otherLogin">
						——<span>或用第三方账号登录</span>——
						<ul>
							<li>
								<i class="iconfont">&#xf003c;</i>
							</li>
							<li>
								<i class="iconfont">&#xe635;</i>
							</li>
						</ul>
					</div>
				</div>
			</div>
		</transition>
		
		<transition enter-active-class="animated fadeInRight">
			<div v-show="showRegister">
				<header>
					<mt-header title="注册"  @click="toRegister()">
						<i class="iconfont" slot="left" @click="back">&#xe60e;</i>
						<span slot="right" @click="toLogin()">登录</span>
					</mt-header>
				</header>
				<!--<header>
					<i class="iconfont" @click="back">&#xe60e;</i>
					<span class="login" @click="toRegister()">{{msg1}}</span>
					<span class="register" @click="toLogin()">{{msg2}}</span>
				</header>-->
				<div class="registerBody">
					<div class="registerInfo">
						<p>
							<span>+86</span>
							<input type="text" placeholder="手机号" />
						</p>
						<p>
							<input type="text" placeholder="短信验证码 " />
							<span>获取验证码</span>
						</p>
						
						<input type="password" placeholder="请输入密码(6-32位非空字符) " />
					</div>
					<button>注册</button>
					<p class="agm">
						点击上面的 “注册” 按钮，即代表你已同意
						<span>《POCO摄影用户许可协议》</span>
					</p>
				</div>
				
			</div v-show="showRegister">
		</transition>

				
	</main>
</template>

<script>
	export default{
		data(){
			return{
//				msg1:'登录',
//				msg2:'注册',
				showLogin:true,
				showRegister:false
			}
		},
		methods:{
			back(){
				this.$router.go(-1)
			},
			toLogin(){
//				this.msg1='登录',
//				this.msg2='注册',
				this.showLogin=true,
				this.showRegister=false
			},
			toRegister(){
//				this.msg1='注册',
//				this.msg2='登录',
				this.showLogin=false,
				this.showRegister=true
			}
		}
	}
</script>

<style scoped="scoped" src="../../assets/css/login.css"></style>
<!--<style scoped="scoped">
	@import url("../../assets/css/login.css");
</style>-->



